<template>
  <div id="app">
    <div id="content">
      <link href="https://fonts.googleapis.com/css?family=Montserrat:400,8e 00" rel="stylesheet">
      <div :class="['alert', { errorAlert: messageStore.isError }]" v-if="messageStore.show">
        {{messageStore.message}}
      </div>
      <div class="head">
        <div class="title-container">
          <img class="logo" src="https://github.com/Owltutoring/WebsiteImages/blob/master/owl.png?raw=true">
          <h1 class="title">Owl Tutoring</h1>
        </div>
        <div class="nav-grid" v-if="AccountStore.account != null">
          <router-link class= "nav-item nav-link" to="/">Home</router-link>
          <router-link class= "nav-item nav-link" to="/Tutors">Tutors</router-link>
          <router-link class= "nav-item nav-link" to="/account">Account</router-link>
          <router-link class= "nav-item nav-link" to="/sessions">Sessions</router-link>
          <button class= "nav-item color-button" v-on:click="logout()">logout</button>  
        </div>
        <div class="nav-grid" v-else>
          <router-link class= "nav-item nav-link" to="/">Home</router-link>
          <router-link class= "nav-item nav-link" to="/Tutors">Tutors</router-link>
          <router-link class= "nav-item nav-link" to="/SignUp">Sign Up</router-link>
          <router-link class= "nav-item nav-link" to="/login">Login</router-link>
        </div>
      </div>
      <router-view/>
    </div>
    <footer>
      <div><a href="mailto:general@owltutoring.co">general@owltutoring.co</a></div>
      <div>Owl Tutoring {{year}}</div>
      <div> text <a href="sms://2248149291">(224) 814-9291</a></div>
    </footer>
  </div>
</template>

<script>
import AccountStore from "./stores/AccountStore";
import MessageStore from "./stores/MessageStore";

export default {
  name: "app",
  data: function() {
    return {
      AccountStore: AccountStore.data,
      messageStore: MessageStore.data,
      year: new Date().getFullYear()
    };
  },
  methods: {
    logout: function() {
      AccountStore.methods.logout();
    }
  },
  created: function() {
    AccountStore.methods.refreshAccount();
    
  }
};
</script>

<style>
#app {
  font-family: 'Montserrat', sans-serif;
  font-weight: 400;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #2c3e50;
  margin: 0;
}
.grid-margin-container {
  display: grid;
  grid-auto-columns: auto auto auto;
  margin: 0;
}
.after-margin {
  grid-column: 3 / 3;
}
#content {
  min-height: 100vh;
}
body {
  margin: 0;
  padding: 0;
  
}
h1 {
  font-weight: 400;
}
h2 {
  font-weight: 400;
}
.color-button {
  background-color: #ffb219;
  color: white;
  border-radius: 0.2em;
  font-size: 1em;
  padding: 0.3em 2em;
  text-decoration: none;
  font-family: 'Montserrat', sans-serif;
  text-transform: uppercase;
  font-weight: 800;
  text-align: center;
  white-space: nowrap;
  border-color: #ffb219;
  border-width: 0.1em;
  border-style: solid;
  display: inline-block;
}
.plain-button {
  font-family: 'Montserrat', sans-serif;
  background-color: white;
  color: black;
  border-radius: 0.2em;
  font-size: 1em;
  padding: 0.3em 2em;
  border: solid;
  border-color: grey;
  border-width: 0.1em;
  text-decoration: none;
  text-transform: uppercase;
  font-weight: 800;
  text-align: center;
  white-space: nowrap;
}
.light-button {
  border-width: 0.1em;
  padding: 0.3em 2em;
  border-radius: 0.2em;
  border-color: #cccccc;
  border-style: solid;


  background-color: white;
  border-radius: 0.2em;
  font-size: 1em;
  padding: 0.3em 2em;
  text-decoration: none;
  font-family: 'Montserrat', sans-serif;
  text-transform: uppercase;
  font-weight: 800;
  text-align: center;
  white-space: nowrap;
}
.nav-grid {
  display: flex;
  align-items: stretch;
  flex-direction: row;
  justify-content: space-around;
}
.nav-item {
  display: flex;
  align-items: center;
  font-size: 1.5em;
}
.nav-link {
  text-decoration: none;
  color: inherit;
}

.nav-link:hover {
  color: grey;
}
.alert {
  position: fixed;
  padding: 0.3em;
  font-size: 1.5em;
  border-radius: 0.5em;
  background-color: lime;
  width: 50%;
  margin-top: 5vh;
  margin-left: 25%;
  margin-right: 50%;
  text-align: center;
  color: white;
  animation: popInOut 3.1s ease-in-out backwards 1;
  z-index: 5;
}
.errorAlert {
  background-color: tomato;
}
.logo {
  width:5em;
  height:5em;
}
input {
  font-family: 'Montserrat', sans-serif;
}
a {
  color: inherit;
}
.title-container {
  display:flex;
}
@keyframes popInOut {
  100% {
    transform: translateY(-200px);
  }
  80% {
    transform: translateY(0px);
  }
  20% {
    transform: translateY(0px);
  }
  0% {
    transform: translateY(-50vh);
  }
}
.head {
  background-color: black;
  color: white;
  margin: 0;
  padding: 1em;
  padding-bottom: 1.5em;
  margin: 0;
}
footer {
  color: white;
  background-color: black;
  text-align: center;
  padding: 2em;
  display:flex;
  justify-content: space-around;
}
.form-field {
  font-size: 1.2em;
  padding: 7px;
  border-style: solid;
  border-radius: 5px;
  border-color: #cccccc;
  border-width: 2px;
}
</style>



